pre {
  border: 0;
  border-left: 4px solid #D7DBE4;
  background: #F0F3F8;
}
pre code {
  padding: 0;
  font-size: inherit;
  color: inherit;
  white-space: pre-wrap;
  background-color: transparent;
  border: 0;
}
.highlight .nt {
  color: #000080;
}
.highlight .na {
  color: #008080;
}
.highlight .s {
  color: #d14;
}