.et-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  z-index: 1001;
  .et-modal-dialog {
    width: 900px;
    height: 400px;
    margin: 70px auto;
    border: 8px solid #666;
    border-color: rgba(0, 0, 0, .25);
    box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
    background-clip: padding-box;
    background-color: #fff;
    .et-modal-content {
      border-radius: 0;
      .et-modal-header {
        position: relative;
        height: 0;
        padding: 0;
        border: 0;
        z-index: 1001;
        min-height: 0;
        .close {
          position: absolute;
          right: 15px;
          top: 15px;
          float: right;
          font-size: 21px;
          font-weight: 700;
          line-height: 1;
          color: #000;
          text-shadow: 0 1px 0 #fff;
          filter: alpha(opacity=20);
          opacity: .2;
          margin-top: -2px;
          font-family: Arial;
          outline: none;
        }
        .close:focus,
        .close:hover {
          color: #000;
          text-decoration: none;
          cursor: pointer;
          filter: alpha(opacity=50);
          opacity: .5;
        }
      }
      .et-modal-body {
        padding: 0;
        margin: 0;
        .title-bar {
          background: #f8fafe;
          border-bottom: 1px solid #e5e5e5;
          min-height: 46px;
          padding: 13px 20px;
          vertical-align: bottom;
          position: relative;
          .heading {
            font-size: 14px;
            line-height: 20px;
            font-weight: bold;
          }
        }
      }
    }
  }
}

.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  background-color: #000;
}

.modal-backdrop.fade {
  filter: alpha(opacity=0);
  opacity: 0;
}

.modal-backdrop.in {
  filter: alpha(opacity=50);
  opacity: .5;
}
