body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "微软雅黑", "open sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

ul,
ol,
li,
dl,
dt,
dd {
  list-style: none;
  border: 0;
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
}

#wrapper {
  height: 100%;
}

#wrapper #page-wrapper .page-content {
  padding: 10px;
}

.pace-done .navbar-static-side,
.pace-done .nav-header,
.pace-done li.active,
.pace-done #page-wrapper,
.pace-done .footer {
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}


/*full-height*/

body.full-height {
  background-color: #2f4050;
}

body.full-height #page-wrapper {
  margin: 0 0 0 220px;
}

body.full-height #page-wrapper .page-content {
  background-color: #fff;
  padding-bottom: 1px;
}

body.full-height.mini-navbar #page-wrapper {
  margin: 0 0 0 70px;
}


/*fixed-sidebar*/

body.fixed-sidebar #page-wrapper {
  padding-left: 200px;
  -webkit-height: calc(~"100% - @{header-height}");
  height: calc(~"100% - @{header-height}");
}

body.fixed-sidebar.mini-navbar #page-wrapper {
  padding-left: 0;
}

body.fixed-sidebar .page-content {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #fff;
  float: left;
  width: 100%;
}


/*canvas-menu*/

body.canvas-menu.mini-navbar #page-wrapper {
  margin: 0 0 0 0px;
}


/*simple-layout*/

body.simple-layout {
  .page-content {
    width: @page-center-width;
    margin: 0 auto;
  }
}
