.navbar-default {
  /*display: none;*/
  background: #444;
  color: #f2f2f2;
}

.navbar-default .nav > li > a:hover,
.navbar-default .nav > li > a:focus {
  background-color: #333;
  color: #fff;
}

.nav > li > a {
  color: #a7b1c2;
  font-weight: 500;
  padding: 14px 20px 14px 25px;
}

.nav > li.active {
  border-left: 4px solid #19aa8d;
  background: #333;
}

.nav > li.active > a {
  color: #ffffff;
}

.nav-second-level li a {
  padding: 7px 10px 7px 10px;
  padding-left: 52px;
}


/*full-height*/

body.full-height nav.navbar-static-side {
  position: absolute;
  width: 220px;
  z-index: 999;
  display: block;
}

body.full-height.mini-navbar nav.navbar-static-side {
  width: 70px;
}


/*fixed-sidebar*/

body.fixed-sidebar .navbar-static-side {
  height: 100%;
  float: left;
  margin-left: -100%;
  position: relative;
  left: -200px;
}

body.fixed-sidebar .navbar-static-side nav {
  width: 200px;
  height: 100%;
  overflow-x: hidden;
}

body.fixed-sidebar .navbar-static-side .sidebar-collapse {
  width: 220px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

body.fixed-sidebar .navbar-static-side .sidebar-collapse .nav {
  width: 200px;
}


/*body.fixed-sidebar.mini-navbar .navbar-static-side {
  width: 0;
}*/

body.fixed-sidebar .etstyle-product-navbar-collapse {
  position: absolute;
  right: 0;
  top: 50%;
  width: 20px;
  height: 50px;
  z-index: 99;
  -o-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse {
  right: -20px;
}

body.fixed-sidebar .etstyle-product-navbar-collapse .product-navbar-collapse-inner {
  top: -50%;
  position: relative;
  overflow: hidden;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse .product-navbar-collapse-bg {
  top: 0;
  left: 0;
  border-bottom: 9px solid transparent;
  border-left: 13px solid #D9DEE4;
  border-right: none;
  border-top: 9px solid transparent;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse:hover .product-navbar-collapse-bg {
  border-bottom: 8px solid transparent;
  border-left: 20px solid #D9DEE4;
  border-right: none;
  border-top: 8px solid transparent;
}

body.fixed-sidebar .etstyle-product-navbar-collapse:hover .product-navbar-collapse-bg {
  border-bottom: 8px solid transparent;
  border-left: none;
  border-right: 20px solid #f7f7f7;
  border-top: 8px solid transparent;
}

body.fixed-sidebar .etstyle-product-navbar-collapse .product-navbar-collapse-bg {
  width: 0;
  height: 50px;
  position: absolute;
  right: 0;
  left: auto;
  border-bottom: 9px solid transparent;
  border-left: none;
  border-right: 13px solid #f7f7f7;
  border-top: 9px solid transparent;
  -o-transition: all 0.1s ease, 0.1s ease;
  -ms-transition: all 0.1s ease, 0.1s ease;
  -moz-transition: all 0.1s ease, 0.1s ease;
  -webkit-transition: all 0.1s ease, 0.1s ease;
}

body.fixed-sidebar .etstyle-product-navbar-collapse .product-navbar-collapse {
  height: 50px;
  position: relative;
  right: -7px;
  text-align: center;
  cursor: pointer;
  -o-transition: all 0.1s ease, 0.1s ease;
  -ms-transition: all 0.1s ease, 0.1s ease;
  -moz-transition: all 0.1s ease, 0.1s ease;
  -webkit-transition: all 0.1s ease, 0.1s ease;
}

body.fixed-sidebar .etstyle-product-navbar-collapse:hover .product-navbar-collapse {
  right: 0;
  left: auto;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse .product-navbar-collapse {
  left: -7px;
  right: auto;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse:hover .product-navbar-collapse {
  left: 0;
  right: auto;
}

body.fixed-sidebar .etstyle-product-navbar-collapse .product-navbar-collapse .fa-indent {
  display: none;
  color: #546478;
  font-size: 15px;
  line-height: 50px;
  vertical-align: text-top;
}

body.fixed-sidebar .etstyle-product-navbar-collapse .product-navbar-collapse .fa-outdent {
  display: inline;
  color: #546478;
  font-size: 15px;
  line-height: 50px;
  vertical-align: text-top;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse .product-navbar-collapse .fa-indent {
  display: inline;
}

body.fixed-sidebar.mini-navbar .etstyle-product-navbar-collapse .product-navbar-collapse .fa-outdent {
  display: none;
}


/*canvas-menu*/

body.canvas-menu nav.navbar-static-side {
  z-index: 2001;
  background: #2f4050;
  height: 100%;
  position: fixed;
  display: none;
  overflow: hidden;
}

body.canvas-menu.mini-navbar nav.navbar-static-side {
  width: 220px;
  display: block;
}

a.close-canvas-menu {
  position: absolute;
  top: 10px;
  right: 15px;
  z-index: 1011;
  color: #a7b1c2;
}


/*simple-layout*/

body.simple-layout nav.navbar-static-side {
  display: none;
}

@media (max-width: 768px) {
  #page-wrapper {
    position: inherit;
    margin: 0 0 0 0px;
    min-height: 1000px;
  }
  .body-small .navbar-static-side {
    display: none;
    z-index: 999;
    position: absolute;
    width: 70px;
  }
  .fixed-sidebar.body-small nav.navbar-static-side {
    width: 0;
  }
  .body-small.mini-navbar .navbar-static-side {
    display: block;
  }
  .lock-word {
    display: none;
  }
  .navbar-form-custom {
    display: none;
  }
  .navbar-header {
    display: inline;
    float: left;
  }
  .sidebard-panel {
    z-index: 2;
    position: relative;
    width: auto;
    min-height: 100% !important;
  }
  .sidebar-content .wrapper {
    padding-right: 0px;
    z-index: 1;
  }
  .fixed-sidebar.body-small .navbar-static-side {
    display: none;
    z-index: 999;
    position: fixed;
    width: 220px;
  }
  .fixed-sidebar.body-small.mini-navbar .navbar-static-side {
    display: block;
  }
  .ibox-tools {
    float: none;
    text-align: right;
    display: block;
  }
}
