
/*full-height*/

body.full-height {
  .header {
    height: @header-height;
    background: @header-color;
  }
}


/*fixed-sidebar*/

body.fixed-sidebar {
  .header {
    height: @header-height;
    background: @header-color;
    .topbar-head {
      font-size: 0;
      a {
        display: inline-block;
        text-decoration: none;
        line-height: @header-height;
        padding: 0 20px;
        color: #fff;
        font-size: 14px;
      }
      a:hover {
        background: #008fbf; 
      }
      .topbar-logo {
        background: #008fbf; 
      }
      .line-right {
        border-right: 1px solid #008fbf;
      }
      .line-left {
        border-left: 1px solid #008fbf;
      }
    }
    .topbar-left {
      float: left;
    }
    .topbar-right {
      float: right;
    }
  }
}


/*canvas-menu*/

body.canvas-menu {
  .header {
    height: @header-height;
    background: @header-color;
  }
}


/*simple-layout*/

body.simple-layout {
  .header {
    height: @header-height;
    background: @header-color;
  }
}
